@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&family=Material+Icons&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}body{margin-left:auto;margin-right:auto;padding:1rem;overflow-x:hidden}.flex-container{display:flex;flex-wrap:wrap;justify-content:space-between}.content{margin-top:10px;margin-bottom:10px}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

body{margin:0;font-family:"Roboto", sans-serif, -apple-system, BlinkMacSystemFont;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}header{font-size:1.6rem;text-align:center;padding:10px 20px;color:#424242;box-shadow:1px 1px 8px rgba(0,0,0,0.1);position:-webkit-sticky;position:sticky;top:0;background:#fff;z-index:10}.content{max-width:1200px;margin:20px auto;display:flex;flex-direction:column;align-items:center}.error{background-color:#ffa07a;padding:10px;border-radius:4px;border:1px solid tomato}.grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));padding:25px;grid-gap:25px;grid-auto-flow:dense;align-items:stretch;box-sizing:border-box}.grid,img{width:100%}img{height:100%;object-fit:cover;border-radius:2px}.item-1{grid-row:span 1}.item-2{grid-row:span 2}.item-3{grid-row:span 3}.item-4{grid-row:span 4}.item-5{grid-row:span 5}.item{position:relative}.pb-25{padding-bottom:25px}.grey{color:#424242}

